<template>
  <a-row type="flex" justify="end"  style="min-width: 1400px">
    <a-col style="width: 85%">
      <div :style="{minHeight: '280px' }">
        <div class="mlist-control ant-list-bordered">
          <div class="list-control-finish">
            <h3>管理员账号列表</h3>
          </div>
          <div style="margin-left: auto;">
            <router-link to="/newManger" class="list-control-new">
              <a-button type="primary" ghost style="width:100px">添加</a-button>
            </router-link>
            <a-button
              type="primary"
              ghost
              style="width:100px"
              @click="manageId"
              :disabled="!hasSelected"
            >批量删除</a-button>
          </div>
        </div>

        <a-card style="width: 90% ;margin: 0 auto;height: 80%;min-width: 1200px" :bordered="true"
                :style="{backgroundImage: 'url(' + img + ')',
          backgroundRepeat:no-repeat,center,top,
          backgroundSize:'100% 100%'
          }">
          <a-table
            :columns="columns"
            :dataSource="managerList"
            :pagination="{ pageSize: 6 }"
            :rowSelection="{selectedRowKeys: index, onChange: onSelectChange}"
          ></a-table>
        </a-card>
        <a-layout-footer style="textAlign: center">DDDB ©2019 Created by 六芒星</a-layout-footer>
      </div>
    </a-col>
  </a-row>
</template>
<script>
import { getAllManagers, deletManager } from "../../api/manRequest";
const columns = [
  {
    title: "用户ID",
    dataIndex: "id",
    key: "id",
    width: "50%"
  },
  {
    title: "成员姓名",
    dataIndex: "name",
    key: "name",
    width: "50%"
  }
];
export default {
  components: {},
  data() {
    return {
      columns,
      index: [],
      deletList:[],
      id: localStorage.getItem("userId"),
      groupId: this.$route.query.groupId,
      managerList: [],
      img:require('../../assets/dddb-webEnt/outbg.jpg')
    };
  },
  computed: {
    hasSelected() {
      return this.index.length > 0;
    },
    list() {
      // return this.$store.state.taskList;
    },
    paginationProps() {
      let pag = {
        pageSize: 6,
        total: this.data.length
      };
      return pag;
    },
    data() {
      let list = [...this.groupList];
      return list;
    }
  },
  mounted() {
    this.getGroupList();
  },
  created() {
    this.$parent.showSiderMenu = true;
    this.$parent.showMenu = false;
  },
  methods: {
    onSelectChange(index) {
      let constList = []
      let managerList = this.managerList
      for (let i = 0; i < index.length; i++) {
        for (let j = 0; j < managerList.length; j++) {
          if (managerList[j].index == index[i]) {
            constList[i]=managerList[j].id
          }
        }
      }
      this.index = index;
      this.deletList = constList;
    },
    manageId() {
      const that = this;
      this.$confirm({
        title: "确定要删除这些管理员吗?",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          const deletManagerList = {del_list: that.deletList};
          deletManager(deletManagerList)
            .then(response => {
              that.index = []
              that.managerList = response.data.data;
              alert('删除成功');
            })
            .catch(function(error) {
              console.log(error);
            });
          console.log("OK");
        },
        onCancel() {
          console.log("Cancel");
        }
      });
    },
    getGroupList() {
      getAllManagers()
        .then(response => {
          this.managerList = response.data.data;
          console.log(this.managerList);
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>

<style lang="less" scoped>
.list-control {
  background: #fff;
  width: 90%;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 16px;
  /*height: 60px;*/
  display: flex;
  flex-direction: row;
}
.list-control-finish {
  margin-right: 50px;
}
.list-control-new {
  width: 150px;
  margin-left: 450px;
}
.listpage {
  min-height: 500px;
  background: #fff;
  margin: 0 50px;
  margin-bottom: 20px;
  padding: 16px 40px 0px 40px;
  width: 100px;
}
.card-list {
  /deep/ .ant-card-body:hover {
    .ant-card-meta-title > a {
      color: #f5222d;
    }
  }

  /deep/ .ant-card-meta-title {
    margin-bottom: 12px;

    & > a {
      display: inline-block;
      max-width: 100%;
      color: rgba(0, 0, 0, 0.85);
    }
  }

  /deep/ .meta-content {
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    height: 64px;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;

    margin-bottom: 1em;
  }
}

.affix-bottom {
  background: #b80c0a;
  width: 100%;
  margin: 0 auto;
  height: 80px;
  padding: 20px 0 0 20px;
}
.ant-card-actions {
  background: #f7f9fa;

  li {
    float: left;
    text-align: center;
    margin: 12px 0;
    color: rgba(0, 0, 0, 0.45);
    width: 50%;

    &:not(:last-child) {
      border-right: 1px solid #e8e8e8;
    }

    a {
      color: rgba(0, 0, 0, 0.45);
      line-height: 22px;
      display: inline-block;
      width: 100%;
      &:hover {
        color: #f5222d;
      }
    }
  }
}

.new-btn {
  background-color: #fff;
  border-radius: 2px;
  width: 100%;
  height: 188px;
}

.vcenter {
  position: absolute;
  left: 50%;
  top: 50%;
}
.listpage {
  min-height: 50px;
  background: #fff;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 16px 10px 0px 10px;
  width: 100%;
}
</style>



